<template>
  <div>
    <base-header class="pb-6">
      <div class="row align-items-center py-4">
        <div class="col-lg-6 col-7">
          <h6 class="h2 text-white d-inline-block mb-0">Lesson Types</h6>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block ml-md-4">
            <route-breadcrumb />
          </nav>
        </div>
        <div class="col-lg-6 col-5 text-right">
          <base-button size="sm" type="neutral" @click.prevent="showPreview = false; showCreate = true">Create</base-button>
        </div>
      </div>
    </base-header>
    <div class="container-fluid mt--6">
      <div class="row">
        <div class="col-lg-4">
          <inline-actions-table @preview="preview"></inline-actions-table>
        </div>
        <div class="col-lg-8 card-wrapper" v-if="showPreview || showCreate">
          <div class="card">
            <div class="card-header">
              <div class="row align-items-center">
                <div class="col-8">
                  <h5 class="h3 mb-0" v-if="showPreview">Lesson Type Detail</h5>
                  <h5 class="h3 mb-0" v-else>New Lesson Type</h5>
                </div>
                <div class="col-4 text-right">
                  <span
                    class="btn btn-sm btn-danger tk-font-color-white"
                    v-if="showPreview"
                    @click.prevent="deleteLessonType"
                    >Delete</span
                  >
                  <span
                    class="btn btn-sm btn-main tk-font-color-white"
                    @click.prevent=""
                    >Save</span
                  >
                  <span
                    class="btn btn-sm btn-neutral tk-font-color-main"
                    @click.prevent="showPreview = false; showCreate = false; initDuration()"
                    >Collapse</span
                  >
                </div>
              </div>
            </div>
            <div class="card-body">
              <!-- name -->
              <span>
                <div class="form-group card-wrapper tk-px-margin-bottom-px0">
                  <label class="form-control-label">Name of lesson type</label>
                  <div class="card tk-px-margin-bottom-px16">
                    <div class="has-label card-body tk-px-padding-px0">
                      <input
                        type="text"
                        class="form-control tk-font-color-black1"
                        v-model="previewData.lessonTypeName"
                        v-if="showPreview"
                      />
                      <input
                        type="text"
                        class="form-control tk-font-color-black1"
                        v-model="createData.lessonTypeName"
                        v-else
                      />
                    </div>
                  </div>
                </div>
              </span>
              <!-- instrument -->
              <span>
                <div class="form-group card-wrapper tk-px-margin-bottom-px0">
                  <div
                    class="card tk-border-color-gray-300 tk-px-margin-bottom-px16"
                  >
                    <div class="card-body tk-px-padding-px16">
                      <div
                        class="avatar rounded-circle tk-layout-inline-block tk-px-margin-right-px10 tk-bg-color-gray-200 tk-border-color-gray-200"
                      >
                        <img v-if="showPreview" :src="previewData.image" />
                        <img v-else :src="createData.image" />
                      </div>
                      <div
                        class="tk-layout-inline-block tk-font-color-main tk-text-right tk-cursor-pointer"
                        style="width: calc(100% - 64px)"
                        @click.prevent="showInstruments = !showInstruments"
                      >
                        <span class="tk-layout-inline-block text-xs"
                          >Select an icon</span
                        >
                        <i
                          class="fas fa-angle-down tk-layout-inline-block tk-px-margin-left-px8"
                        ></i>
                      </div>
                      <div v-if="showInstruments" class="tk-text-center">
                        <template v-for="(item, index) in instruments">
                          <div
                            :key="index"
                            class="tk-layout-inline-block tk-px-margin-top-px16 tk-px-margin-right-px16 tk-cursor-pointer"
                            @click.prevent="
                              previewData.image = item.url;
                              createData.image = item.url;
                              showInstruments = false;
                            "
                          >
                            <div
                              class="avatar avatar-lg rounded-circle tk-bg-color-gray-200 tk-border-color-gray-200"
                            >
                              <img :src="item.url" />
                            </div>
                            <div class="tk-font-color-lightGray text-xs">
                              {{ item.name }}
                            </div>
                          </div>
                        </template>
                      </div>
                    </div>
                  </div>
                </div>
              </span>
              <!-- duration -->
              <span>
                <div class="form-group card-wrapper tk-px-margin-bottom-px0">
                  <label class="form-control-label">Duration(min)</label>
                  <div
                    class="card tk-border-color-gray-300 tk-px-margin-bottom-px16"
                  >
                    <div class="has-label card-body tk-px-padding-px16">
                      <template v-for="(item, index) in durations">
                        <div
                          class="tk-layout-inline-block tk-px-radius-px5 tk-px-padding-top-px10 tk-px-margin-right-px10 tk-px-padding-bottom-px10 tk-text-center tk-cursor-pointer"
                          :class="{
                            'tk-border-color-main tk-font-color-main':
                              item.selected,
                            'tk-border-color-gray-300 tk-font-color-gray': !item.selected,
                            'tk-px-margin-bottom-px10': index < 4,
                          }"
                          :key="index"
                          style="width: calc(25% - 10px)"
                          @click.prevent="selectDuration(item)"
                        >
                          {{ item.length }}
                        </div>
                      </template>
                    </div>
                  </div>
                </div>
              </span>
              <!-- package -->
              <span>
                <div class="form-group card-wrapper tk-px-margin-bottom-px0">
                  <div
                    class="card tk-border-color-gray-300 tk-px-margin-bottom-px16"
                  >
                    <div class="card-body tk-px-padding-px16">
                      <span
                        class="tk-layout-inline-block tk-font-color-black1"
                        style="width: calc(100% - 56px)"
                        >Package option</span
                      >
                      <base-switch
                        v-if="showPreview"
                        class="mr-1 tk-layout-inline-block"
                        v-model="previewData.hasPackage"
                        on-text="On"
                        off-text="Off"
                        type="main"
                      ></base-switch>
                      <base-switch
                        v-else
                        class="mr-1 tk-layout-inline-block"
                        v-model="createData.hasPackage"
                        on-text="On"
                        off-text="Off"
                        type="main"
                      ></base-switch>
                      <div
                        class="invalid-feedback tk-font-color-gray tk-px-margin-top-px10"
                        style="display: block"
                        v-if="previewData.hasPackage || createData.hasPackage"
                      >
                        <base-button
                          style="
                            padding: 2px 5px;
                            background-color: rgba(176, 238, 211, 0.48);
                            box-shadow: none;
                            z-index: 1
                          "
                          class="tk-font-color-green tk-position-relative tk-border-none tk-px-margin-right-px10"
                          @click.prevent="showPackageList = !showPackageList"
                        >
                          <span v-if="showPreview">{{ previewData.package }} lesson(s)</span>
                          <span v-if="showCreate">{{ createData.package }} lesson(s)</span>
                          <i class="fas fa-caret-down tk-px-margin-right-px0"></i>
                          <item-list :data="packageList" :value="createData.package" v-if="showPackageList" class="tk-position-absolute" @select="select"></item-list>
                        </base-button>
                        <span>required</span>
                      </div>
                    </div>
                  </div>
                </div>
              </span>
              <!-- price -->
              <span>
                <div class="form-group card-wrapper tk-px-margin-bottom-px0">
                  <label class="form-control-label">Price / lesson</label>
                  <div class="card tk-px-margin-bottom-px8">
                    <div class="has-label card-body tk-px-padding-px0">
                      <input
                        v-if="showPreview"
                        type="text"
                        class="form-control tk-font-color-black1"
                        @keypress.enter.prevent=""
                        v-model="previewData.price"
                      />
                      <input
                        v-else
                        type="text"
                        class="form-control tk-font-color-black1"
                        @keypress.enter.prevent=""
                        v-model="createData.price"
                      />
                    </div>
                  </div>
                  <div
                    class="invalid-feedback tk-font-color-lightGray"
                    style="display: block"
                  >
                    Invisible for students
                  </div>
                </div>
              </span>
            </div>
          </div>
        </div>
        <div class="col-lg-8 card-wrapper tk-text-center" v-else>
          <img
            src="/img/icons/tk/img_type.png"
            class="tk-px-margin-top-px100"
            style="width: calc(100% - 80px)"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import bus from "@/bus.js";
import lessonTypes from "./LessonTypes/lessonTypes"
export default {
  components: {
    InlineActionsTable: () => import("./LessonTypes/RegularTables/InlineActionsTable"),
    ItemList: () => import('../components/layout/ItemList.vue'),
  },
  data() {
    return {
      showPreview: false,
      showCreate: false,
      showPackageList: false,
      previewData: {},
      createData: {
        lessonTypeName: '',
        image: '',
        duration: '',
        hasPackage: false,
        package: 5,
        price: ''
      },
      showInstruments: false,
      instruments: [],
      durations: [],
      lessonTypes,
      packageList: []
    };
  },
  created() {
    let self = this
    self.instruments = self.$tools.instruments
    self.durations = self.$tools.durations
    
    for (let i = 0; i < 50; i++) {
      self.packageList.push({
        label: i > 0 ? ((i + 1) + ' lessons') : ((i + 1) + ' lesson'),
        value: i + 1
      })
    }
  },
  methods: {
    select(item) {
      let self = this
      self.previewData.package = item.value
      self.createData.package = item.value
    },
    preview(data) {
      let self = this;
      self.showPreview = true;
      self.showCreate = false;
      self.previewData = data;
      self.durations.some(item => {
        if (item.length === parseInt(self.previewData.duration.split("min")[0])) {
          item.selected = true
        }
      })
    },
    selectDuration(item) {
      let self = this;
      self.durations.some((time) => {
        time.selected = false;
      });
      item.selected = true;
      self.previewData.duration = item.length;
      self.createData.duration = item.length;
    },
    initDuration() {
      this.durations.some((time) => {
        time.selected = false;
      });
      this.initCreateData()
    },
    initCreateData() {
      this.createData =  {
        lessonTypeName: '',
        image: '',
        duration: '',
        hasPackage: false,
        package: '',
        price: ''
      }
    },
    isNull(data) {
      return this.$tools.isNull(data)
    },
    deleteLessonType() {
      let self = this
      bus.$emit("onDelete", self.previewData);
      self.showPreview = false
    }
  },
};
</script>